body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.gradient {
  background: #466368;
  background: -webkit-radial-gradient(#FEFEFE,#61BCC1);
  background: radial-gradient(#FEFEFE,#61BCC1);
  /* border-radius: 6px;
  height: 120px; */
  width: 1360px;
  height: 580px;
}

.yellowButton{
  background: -webkit-radial-gradient(#FEEC21,#E0A821);
  background: radial-gradient(#FEEC21,#E0A821);
  border-radius: 200px;
  height: 400px;
  width: 400px;
  align-items: center;
}
.yellowButton:hover{
  background: -webkit-radial-gradient(#e5d02d,#d19917);
  background: radial-gradient(#e5d02d,#d19917);
}

.whiteSpace{
  background-color: white;
  width:300px;
  height: 100px;
}

.flexContainer{
  display: flex;
  flex-wrap: nowrap;
  
}

.ctx {
  fill: #005184;
  font-size: 25.1px;
  font-weight: bold;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

